"use client";

import React, { createContext, useContext, useState, ReactNode } from 'react';
import { toast } from "sonner";

export interface WorkflowNode {
  id: string;
  type: string;
  name: string;
  x: number;
  y: number;
  properties: Record<string, any>;
}

interface WorkflowContextType {
  nodes: WorkflowNode[];
  selectedNode: WorkflowNode | null;
  isLoading: boolean;
  addNode: (node: WorkflowNode) => void;
  updateNode: (node: WorkflowNode) => void;
  deleteNode: (nodeId: string) => void;
  selectNode: (node: WorkflowNode | null) => void;
  saveWorkflow: () => Promise<void>;
  runWorkflow: () => Promise<void>;
}

const WorkflowContext = createContext<WorkflowContextType | undefined>(undefined);

export function WorkflowProvider({ children }: { children: ReactNode }) {
  const [nodes, setNodes] = useState<WorkflowNode[]>([]);
  const [selectedNode, setSelectedNode] = useState<WorkflowNode | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const addNode = (node: WorkflowNode) => {
    try {
      setNodes((prev) => [...prev, node]);
      toast.success("Node added successfully");
    } catch (error) {
      toast.error("Failed to add node");
      console.error("Error adding node:", error);
    }
  };

  const updateNode = (node: WorkflowNode) => {
    try {
      setNodes((prev) =>
        prev.map((n) => (n.id === node.id ? node : n))
      );
      toast.success("Node updated successfully");
    } catch (error) {
      toast.error("Failed to update node");
      console.error("Error updating node:", error);
    }
  };

  const deleteNode = (nodeId: string) => {
    try {
      setNodes((prev) => prev.filter((n) => n.id !== nodeId));
      if (selectedNode?.id === nodeId) {
        setSelectedNode(null);
      }
      toast.success("Node deleted successfully");
    } catch (error) {
      toast.error("Failed to delete node");
      console.error("Error deleting node:", error);
    }
  };

  const selectNode = (node: WorkflowNode | null) => {
    setSelectedNode(node);
  };

  const saveWorkflow = async () => {
    setIsLoading(true);
    try {
      // In a real app, this would save to a backend
      const workflow = {
        nodes,
        version: "1.0",
        lastModified: new Date().toISOString(),
      };
      console.log("Saving workflow:", workflow);
      toast.success("Workflow saved successfully");
    } catch (error) {
      toast.error("Failed to save workflow");
      console.error("Error saving workflow:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const runWorkflow = async () => {
    setIsLoading(true);
    try {
      // In a real app, this would execute the workflow
      console.log("Running workflow with nodes:", nodes);
      toast.success("Workflow executed successfully");
    } catch (error) {
      toast.error("Failed to execute workflow");
      console.error("Error running workflow:", error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <WorkflowContext.Provider
      value={{
        nodes,
        selectedNode,
        isLoading,
        addNode,
        updateNode,
        deleteNode,
        selectNode,
        saveWorkflow,
        runWorkflow,
      }}
    >
      {children}
    </WorkflowContext.Provider>
  );
}

export function useWorkflow() {
  const context = useContext(WorkflowContext);
  if (!context) {
    throw new Error('useWorkflow must be used within a WorkflowProvider');
  }
  return context;
}
