"use client";

import { useWorkflow } from "@/components/providers/workflow-provider";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Separator } from "@/components/ui/separator";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useState, useEffect } from "react";
import { toast } from "sonner";
import { Loader2 } from "lucide-react";

export function NodeProperties() {
  const { selectedNode, updateNode, deleteNode, isLoading } = useWorkflow();
  const [name, setName] = useState("");
  const [properties, setProperties] = useState<Record<string, string>>({});

  useEffect(() => {
    if (selectedNode) {
      setName(selectedNode.name);
      setProperties(selectedNode.properties || {});
    } else {
      setName("");
      setProperties({});
    }
  }, [selectedNode]);

  if (!selectedNode) {
    return (
      <div className="w-80 border-l border-gray-800 bg-gray-900 p-4">
        <p className="text-sm text-gray-400">Select a node to view properties</p>
      </div>
    );
  }

  const handleSave = () => {
    if (selectedNode) {
      try {
        updateNode({
          ...selectedNode,
          name,
          properties,
        });
      } catch (error) {
        toast.error("Failed to update node properties");
        console.error("Error updating node:", error);
      }
    }
  };

  const handleDelete = () => {
    if (selectedNode) {
      try {
        deleteNode(selectedNode.id);
      } catch (error) {
        toast.error("Failed to delete node");
        console.error("Error deleting node:", error);
      }
    }
  };

  const getDefaultProperties = () => {
    switch (selectedNode.type) {
      case "trigger":
        return [
          { key: "endpoint", label: "Endpoint", type: "text" },
          { key: "method", label: "Method", type: "select", options: ["GET", "POST", "PUT", "DELETE"] },
          { key: "headers", label: "Headers", type: "text" },
        ];
      case "action":
        return [
          { key: "url", label: "URL", type: "text" },
          { key: "method", label: "Method", type: "select", options: ["GET", "POST", "PUT", "DELETE"] },
          { key: "body", label: "Body", type: "text" },
        ];
      case "logic":
        return [
          { key: "condition", label: "Condition", type: "text" },
          { key: "value", label: "Value", type: "text" },
        ];
      default:
        return [];
    }
  };

  return (
    <div className="w-80 border-l border-gray-800 bg-gray-900">
      <div className="p-4">
        <h2 className="text-lg font-semibold text-white mb-2">Node Properties</h2>
        <p className="text-sm text-gray-400">Configure the selected node</p>
      </div>
      <Separator className="bg-gray-800" />
      <ScrollArea className="h-[calc(100vh-10rem)]">
        <div className="p-4 space-y-6">
          <div className="space-y-2">
            <Label htmlFor="name" className="text-white">
              Name
            </Label>
            <Input
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="bg-gray-800 border-gray-700 text-white"
              disabled={isLoading}
            />
          </div>

          <div className="space-y-4">
            <Label className="text-white">Properties</Label>
            {getDefaultProperties().map((prop) => (
              <div key={prop.key} className="space-y-2">
                <Label htmlFor={prop.key} className="text-sm text-gray-400">
                  {prop.label}
                </Label>
                {prop.type === "select" ? (
                  <Select
                    value={properties[prop.key] || ""}
                    onValueChange={(value) =>
                      setProperties((prev) => ({
                        ...prev,
                        [prop.key]: value,
                      }))
                    }
                    disabled={isLoading}
                  >
                    <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                      <SelectValue placeholder={`Select ${prop.label}`} />
                    </SelectTrigger>
                    <SelectContent>
                      {prop.options?.map((option) => (
                        <SelectItem key={option} value={option}>
                          {option}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                ) : (
                  <Input
                    id={prop.key}
                    value={properties[prop.key] || ""}
                    onChange={(e) =>
                      setProperties((prev) => ({
                        ...prev,
                        [prop.key]: e.target.value,
                      }))
                    }
                    className="bg-gray-800 border-gray-700 text-white"
                    disabled={isLoading}
                  />
                )}
              </div>
            ))}
          </div>

          <div className="flex space-x-2">
            <Button
              onClick={handleSave}
              className="flex-1 bg-white text-black hover:bg-gray-200"
              disabled={isLoading}
            >
              {isLoading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Saving...
                </>
              ) : (
                "Save Changes"
              )}
            </Button>
            <Button
              onClick={handleDelete}
              variant="destructive"
              className="bg-red-500 hover:bg-red-600"
              disabled={isLoading}
            >
              {isLoading ? (
                <Loader2 className="h-4 w-4 animate-spin" />
              ) : (
                "Delete"
              )}
            </Button>
          </div>
        </div>
      </ScrollArea>
    </div>
  );
}
