"use client";

import { useWorkflow, type WorkflowNode } from "@/components/providers/workflow-provider";
import { Card } from "@/components/ui/card";
import { useCallback, useRef, useState } from "react";
import { toast } from "sonner";

export function Canvas() {
  const { nodes, addNode, updateNode, selectNode } = useWorkflow();
  const canvasRef = useRef<HTMLDivElement>(null);
  const [isDragging, setIsDragging] = useState(false);
  const [dragPosition, setDragPosition] = useState({ x: 0, y: 0 });

  const handleDrop = useCallback(
    (e: React.DragEvent) => {
      e.preventDefault();
      const data = e.dataTransfer.getData("node");
      if (data && canvasRef.current) {
        try {
          const nodeData = JSON.parse(data);
          const rect = canvasRef.current.getBoundingClientRect();
          
          // Use the drag position state for more accurate placement
          const x = dragPosition.x;
          const y = dragPosition.y;

          const newNode: WorkflowNode = {
            id: `${nodeData.id}-${Date.now()}`,
            type: nodeData.type,
            name: nodeData.name,
            x,
            y,
            properties: {},
          };

          addNode(newNode);
          toast.success(`Added ${nodeData.name} node`);
          
          // Automatically select the new node
          selectNode(newNode);
        } catch (error) {
          toast.error("Failed to add node to canvas");
          console.error("Error adding node:", error);
        }
      }
      setIsDragging(false);
    },
    [addNode, dragPosition, selectNode]
  );

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    e.dataTransfer.dropEffect = 'move';
    setIsDragging(true);
    if (canvasRef.current) {
      const rect = canvasRef.current.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;
      
      // Snap to grid
      const gridSize = 20;
      setDragPosition({
        x: Math.round(x / gridSize) * gridSize,
        y: Math.round(y / gridSize) * gridSize,
      });
    }
  }, []);

  const handleDragLeave = useCallback(() => {
    setIsDragging(false);
  }, []);

  const handleNodeDragStart = useCallback(
    (e: React.DragEvent, node: WorkflowNode) => {
      const rect = canvasRef.current?.getBoundingClientRect();
      if (rect) {
        const offsetX = e.clientX - rect.left - node.x;
        const offsetY = e.clientY - rect.top - node.y;
        e.dataTransfer.setData(
          "nodeOffset",
          JSON.stringify({ x: offsetX, y: offsetY })
        );
      }
    },
    []
  );

  const handleNodeDrop = useCallback(
    (e: React.DragEvent, targetNode: WorkflowNode) => {
      e.preventDefault();
      e.stopPropagation();
      const offsetData = e.dataTransfer.getData("nodeOffset");
      if (offsetData && canvasRef.current) {
        try {
          const { x: offsetX, y: offsetY } = JSON.parse(offsetData);
          const rect = canvasRef.current.getBoundingClientRect();
          const x = e.clientX - rect.left - offsetX;
          const y = e.clientY - rect.top - offsetY;

          updateNode({
            ...targetNode,
            x,
            y,
          });
        } catch (error) {
          toast.error("Failed to move node");
          console.error("Error moving node:", error);
        }
      }
    },
    [updateNode]
  );

  return (
    <div
      ref={canvasRef}
      className={`flex-1 relative overflow-hidden bg-black transition-colors duration-200 ${
        isDragging ? "bg-opacity-70" : ""
      }`}
      onDrop={handleDrop}
      onDragOver={handleDragOver}
      onDragLeave={handleDragLeave}
    >
      {/* Grid Background */}
      <div className="absolute inset-0 grid grid-cols-[repeat(40,minmax(20px,1fr))] grid-rows-[repeat(40,minmax(20px,1fr))] gap-px opacity-10">
        {Array.from({ length: 1600 }).map((_, i) => (
          <div key={i} className="bg-gray-500" />
        ))}
      </div>

      {/* Drop Zone Indicator */}
      {isDragging && (
        <>
          <div
            className="absolute w-32 h-24 border-2 border-blue-500 border-dashed rounded-lg pointer-events-none transition-all duration-150"
            style={{
              left: dragPosition.x - 64,
              top: dragPosition.y - 48,
              backgroundColor: "rgba(59, 130, 246, 0.1)",
              transform: "scale(1.05)",
            }}
          />
          <div
            className="absolute w-4 h-4 border-2 border-blue-500 rounded-full pointer-events-none"
            style={{
              left: dragPosition.x - 2,
              top: dragPosition.y - 2,
              backgroundColor: "rgba(59, 130, 246, 0.3)",
            }}
          />
        </>
      )}

      {/* Nodes */}
      {nodes.map((node) => (
        <Card
          key={node.id}
          draggable
          className="absolute p-4 cursor-move bg-gray-800 border-gray-700 hover:bg-gray-700 transition-colors min-w-[150px] rounded-lg shadow-lg group"
          style={{
            left: node.x,
            top: node.y,
            transform: "translate(-50%, -50%)",
          }}
          onDragStart={(e) => handleNodeDragStart(e, node)}
          onDrop={(e) => handleNodeDrop(e, node)}
          onClick={() => selectNode(node)}
        >
          <div className="flex flex-col space-y-2">
            <div className="flex items-center space-x-2">
              <div className={`w-2 h-2 rounded-full ${
                node.type === "trigger" ? "bg-blue-500" :
                node.type === "action" ? "bg-green-500" :
                "bg-yellow-500"
              }`} />
              <p className="text-sm font-medium text-white group-hover:text-blue-400 transition-colors">
                {node.name}
              </p>
            </div>
            <div className="text-xs text-gray-400 bg-gray-900/50 px-2 py-1 rounded">
              {node.type}
            </div>
          </div>
          <div className="absolute inset-0 bg-blue-500/10 opacity-0 group-hover:opacity-100 transition-opacity rounded-lg" />
        </Card>
      ))}
    </div>
  );
}
