"use client";

import { Card } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import { useState } from "react";

const nodeTypes = [
  {
    category: "Triggers",
    description: "Start your workflow",
    nodes: [
      { id: "http-trigger", name: "HTTP Trigger", type: "trigger", description: "Trigger on HTTP requests" },
      { id: "schedule-trigger", name: "Schedule", type: "trigger", description: "Run at specified intervals" },
      { id: "webhook-trigger", name: "Webhook", type: "trigger", description: "Listen for webhook events" },
    ],
  },
  {
    category: "Actions",
    description: "Perform operations",
    nodes: [
      { id: "http-request", name: "HTTP Request", type: "action", description: "Make HTTP requests" },
      { id: "send-email", name: "Send Email", type: "action", description: "Send emails" },
      { id: "database-query", name: "Database Query", type: "action", description: "Execute database queries" },
    ],
  },
  {
    category: "Logic",
    description: "Control flow",
    nodes: [
      { id: "if-condition", name: "If Condition", type: "logic", description: "Branch based on conditions" },
      { id: "switch", name: "Switch", type: "logic", description: "Multiple conditional branches" },
      { id: "loop", name: "Loop", type: "logic", description: "Iterate over items" },
    ],
  },
];

export function Sidebar() {
  const [draggedNode, setDraggedNode] = useState<string | null>(null);

  const handleDragStart = (e: React.DragEvent, node: any) => {
    e.dataTransfer.effectAllowed = 'move';
    e.dataTransfer.setData("node", JSON.stringify(node));
    setDraggedNode(node.id);
    
    // Create a drag image
    const dragImage = document.createElement('div');
    dragImage.className = 'p-3 bg-gray-800 border border-gray-700 rounded-lg shadow-lg';
    dragImage.innerHTML = `
      <div class="flex items-center space-x-2">
        <div class="w-2 h-2 rounded-full bg-${
          node.type === "trigger" ? "blue" :
          node.type === "action" ? "green" :
          "yellow"
        }-500"></div>
        <p class="text-sm font-medium text-white">${node.name}</p>
      </div>
    `;
    document.body.appendChild(dragImage);
    e.dataTransfer.setDragImage(dragImage, 75, 25);
    setTimeout(() => document.body.removeChild(dragImage), 0);
  };

  const handleDragEnd = () => {
    setDraggedNode(null);
  };

  return (
    <div className="w-64 border-r border-gray-800 bg-gray-900">
      <div className="p-4">
        <h2 className="text-lg font-semibold text-white mb-2">Nodes</h2>
        <p className="text-sm text-gray-400">Drag nodes to the canvas</p>
      </div>
      <Separator className="bg-gray-800" />
      <ScrollArea className="h-[calc(100vh-5rem)]">
        <div className="p-4 space-y-6">
          {nodeTypes.map((category) => (
            <div key={category.category}>
              <div className="mb-3">
                <h3 className="text-sm font-medium text-gray-400">
                  {category.category}
                </h3>
                <p className="text-xs text-gray-500">{category.description}</p>
              </div>
              <div className="space-y-2">
                {category.nodes.map((node) => (
                  <Card
                    key={node.id}
                    draggable
                    onDragStart={(e) => handleDragStart(e, node)}
                    onDragEnd={handleDragEnd}
                    className={`p-3 cursor-move bg-gray-800 border-gray-700 hover:bg-gray-700 transition-all group relative ${
                      draggedNode === node.id ? "scale-95 opacity-50" : ""
                    }`}
                  >
                    <div className="absolute inset-0 bg-gradient-to-r from-transparent via-blue-500/5 to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />
                    <div className="space-y-1">
                      <div className="flex items-center space-x-2">
                        <div className={`w-2 h-2 rounded-full ${
                          node.type === "trigger" ? "bg-blue-500" :
                          node.type === "action" ? "bg-green-500" :
                          "bg-yellow-500"
                        }`} />
                        <p className="text-sm font-medium text-white group-hover:text-blue-400 transition-colors">
                          {node.name}
                        </p>
                      </div>
                      <p className="text-xs text-gray-400 pl-4">
                        {node.description}
                      </p>
                    </div>
                    <div className="absolute inset-0 bg-blue-500/10 opacity-0 group-hover:opacity-100 transition-opacity rounded-lg" />
                  </Card>
                ))}
              </div>
            </div>
          ))}
        </div>
      </ScrollArea>
    </div>
  );
}
