"use client";

import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { useWorkflow } from "@/components/providers/workflow-provider";
import Link from "next/link";
import { Loader2 } from "lucide-react";

export function Toolbar() {
  const { saveWorkflow, runWorkflow, isLoading } = useWorkflow();

  return (
    <div className="border-b border-gray-800 bg-gray-900 p-2">
      <div className="flex items-center justify-between mx-4">
        <div className="flex items-center space-x-4">
          <Link href="/" className="text-white hover:text-gray-300 font-semibold">
            MyAutoBuild
          </Link>
          <Separator orientation="vertical" className="h-6 bg-gray-800" />
          <div className="flex items-center space-x-2">
            <Button
              onClick={saveWorkflow}
              variant="outline"
              className="bg-transparent border-gray-700 text-white hover:bg-gray-800"
              disabled={isLoading}
            >
              {isLoading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Saving...
                </>
              ) : (
                "Save"
              )}
            </Button>
            <Button
              onClick={runWorkflow}
              className="bg-white text-black hover:bg-gray-200"
              disabled={isLoading}
            >
              {isLoading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Running...
                </>
              ) : (
                "Run Workflow"
              )}
            </Button>
          </div>
        </div>
        <div className="flex items-center space-x-2">
          <Button
            variant="ghost"
            className="text-gray-400 hover:text-white hover:bg-gray-800"
            onClick={() => window.location.reload()}
            disabled={isLoading}
          >
            Reset
          </Button>
        </div>
      </div>
    </div>
  );
}
